"use client";

import { useState, useEffect } from 'react';
import { usePlanet } from '@/context/PlanetContext';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Textarea } from '@/components/ui/textarea';
import { Badge } from '@/components/ui/badge';

const storyPrompts = [
  "Once upon a time, on my magical planet...",
  "The creatures on my planet love to...",
  "When the weather changes, something amazing happens...",
  "My favorite place on the planet is...",
  "The most magical thing about my world is...",
  "One day, a new creature arrived and...",
  "During the candy snow storm...",
  "The flying castle holds a secret...",
  "My planet's greatest adventure began when...",
  "The rainbow rain brought..."
];

const storyTips = [
  "Describe your creatures and their powers",
  "Tell about the magical weather",
  "What adventures happen on your planet?",
  "How do your creatures help each other?",
  "What makes your planet special?",
  "Include the magical places you built"
];

export default function StoryMode() {
  const { state, dispatch } = usePlanet();
  const [story, setStory] = useState(state.story);
  const [wordCount, setWordCount] = useState(0);
  const [selectedPrompt, setSelectedPrompt] = useState('');
  const [showPrompts, setShowPrompts] = useState(true);

  useEffect(() => {
    const words = story.trim().split(/\s+/).filter(word => word.length > 0);
    setWordCount(words.length);
  }, [story]);

  useEffect(() => {
    // Auto-save story every 2 seconds
    const timer = setTimeout(() => {
      if (story !== state.story) {
        dispatch({ type: 'SET_STORY', payload: story });
      }
    }, 2000);

    return () => clearTimeout(timer);
  }, [story, state.story, dispatch]);

  const handleStoryChange = (value: string) => {
    setStory(value);
  };

  const usePrompt = (prompt: string) => {
    setSelectedPrompt(prompt);
    if (story.trim() === '') {
      setStory(prompt + ' ');
    } else {
      setStory(story + '\n\n' + prompt + ' ');
    }
    setShowPrompts(false);
  };

  const clearStory = () => {
    setStory('');
    setSelectedPrompt('');
    dispatch({ type: 'SET_STORY', payload: '' });
  };

  const getRandomPrompt = () => {
    const randomPrompt = storyPrompts[Math.floor(Math.random() * storyPrompts.length)];
    usePrompt(randomPrompt);
  };

  return (
    <div className="space-y-6">
      {/* Story Editor */}
      <Card className="bg-white/90 backdrop-blur-sm">
        <CardHeader>
          <div className="flex justify-between items-center">
            <CardTitle className="text-lg">Your Planet's Story</CardTitle>
            <div className="flex items-center space-x-2">
              <Badge variant="secondary">
                {wordCount} words
              </Badge>
              {story !== state.story && (
                <Badge variant="outline" className="text-xs">
                  Saving...
                </Badge>
              )}
            </div>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          <Textarea
            value={story}
            onChange={(e) => handleStoryChange(e.target.value)}
            placeholder="Start writing your magical story here... Tell us about your planet, creatures, and adventures!"
            className="min-h-[300px] text-base leading-relaxed resize-none"
            maxLength={2000}
          />
          
          <div className="flex justify-between items-center text-sm text-gray-500">
            <span>{story.length}/2000 characters</span>
            <span>Auto-saved</span>
          </div>

          <div className="flex space-x-2">
            <Button
              variant="outline"
              onClick={() => setShowPrompts(!showPrompts)}
              className="flex-1"
            >
              {showPrompts ? 'Hide Prompts' : 'Show Prompts'}
            </Button>
            <Button
              variant="outline"
              onClick={getRandomPrompt}
            >
              Random Prompt
            </Button>
            <Button
              variant="destructive"
              onClick={clearStory}
              disabled={story.length === 0}
            >
              Clear
            </Button>
          </div>
        </CardContent>
      </Card>

      {/* Story Prompts */}
      {showPrompts && (
        <Card className="bg-white/90 backdrop-blur-sm">
          <CardHeader>
            <CardTitle className="text-lg">Story Starters</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 gap-2">
              {storyPrompts.map((prompt, index) => (
                <Button
                  key={index}
                  variant="outline"
                  onClick={() => usePrompt(prompt)}
                  className="text-left justify-start h-auto p-3 whitespace-normal"
                >
                  {prompt}
                </Button>
              ))}
            </div>
          </CardContent>
        </Card>
      )}

      {/* Planet Summary */}
      {(state.landType || state.creatures.length > 0 || state.recordings.length > 0) && (
        <Card className="bg-blue-50 border-2 border-blue-200">
          <CardHeader>
            <CardTitle className="text-lg text-blue-800">Your Planet So Far</CardTitle>
          </CardHeader>
          <CardContent className="space-y-3">
            {state.landType && (
              <div className="flex items-center space-x-2">
                <span className="text-sm font-medium text-blue-700">Land:</span>
                <Badge variant="secondary">{state.landType}</Badge>
              </div>
            )}
            
            {state.skyType && (
              <div className="flex items-center space-x-2">
                <span className="text-sm font-medium text-blue-700">Sky:</span>
                <Badge variant="secondary">{state.skyType}</Badge>
              </div>
            )}
            
            {state.seaType && (
              <div className="flex items-center space-x-2">
                <span className="text-sm font-medium text-blue-700">Sea:</span>
                <Badge variant="secondary">{state.seaType}</Badge>
              </div>
            )}

            {state.creatures.length > 0 && (
              <div className="flex items-center space-x-2">
                <span className="text-sm font-medium text-blue-700">Creatures:</span>
                <div className="flex flex-wrap gap-1">
                  {state.creatures.map((creature) => (
                    <Badge key={creature.id} variant="outline" className="text-xs">
                      {creature.name}
                    </Badge>
                  ))}
                </div>
              </div>
            )}

            {state.recordings.length > 0 && (
              <div className="flex items-center space-x-2">
                <span className="text-sm font-medium text-blue-700">Recordings:</span>
                <Badge variant="secondary">{state.recordings.length} sounds</Badge>
              </div>
            )}

            {state.weather.type !== 'sunny' && (
              <div className="flex items-center space-x-2">
                <span className="text-sm font-medium text-blue-700">Weather:</span>
                <Badge variant="secondary">{state.weather.type}</Badge>
              </div>
            )}
          </CardContent>
        </Card>
      )}

      {/* Writing Tips */}
      <Card className="bg-green-50 border-2 border-green-200">
        <CardContent className="p-4">
          <h3 className="font-bold text-green-800 mb-2">‚úçÔ∏è Writing Tips</h3>
          <ul className="text-sm text-green-700 space-y-1">
            {storyTips.map((tip, index) => (
              <li key={index}>‚Ä¢ {tip}</li>
            ))}
          </ul>
        </CardContent>
      </Card>

      {/* Story Stats */}
      {story.length > 0 && (
        <Card className="bg-purple-50 border-2 border-purple-200">
          <CardContent className="p-4">
            <h3 className="font-bold text-purple-800 mb-2">üìä Story Stats</h3>
            <div className="grid grid-cols-2 gap-4 text-sm">
              <div>
                <span className="text-purple-700 font-medium">Words:</span>
                <span className="ml-2 text-purple-800">{wordCount}</span>
              </div>
              <div>
                <span className="text-purple-700 font-medium">Characters:</span>
                <span className="ml-2 text-purple-800">{story.length}</span>
              </div>
              <div>
                <span className="text-purple-700 font-medium">Paragraphs:</span>
                <span className="ml-2 text-purple-800">
                  {story.split('\n\n').filter(p => p.trim().length > 0).length}
                </span>
              </div>
              <div>
                <span className="text-purple-700 font-medium">Reading Time:</span>
                <span className="ml-2 text-purple-800">
                  {Math.max(1, Math.ceil(wordCount / 200))} min
                </span>
              </div>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
